<template>
  <section>
    <el-form :inline="true">
      <el-form-item label="选择费率字段">
        <el-select
          v-model="value"
          filterable
          remote
          reserve-keyword
          placeholder="请输入关键词"
          :remote-method="remoteMethod"
          :loading="loading"
          @change="selChange"
          multiple
        >
          <el-option
            v-for="item in options"
            :key="item.id"
            :label="`${item.name}${item.fee}%`"
            :value="item.id"
          >
            <span style="float: left; font-size: 11px">{{ item.name }}</span>
            <span style="float: right; color: #8492a6; font-size: 11px">{{
              item.fee
            }}</span>
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="生效日期">
        <el-date-picker
          v-model="datetimerange"
          @change="dateChange"
          type="daterange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          value-format="yyyy-MM-dd"
        >
        </el-date-picker>
      </el-form-item>
    </el-form>
  </section>
</template>

<script>
export default {
  name: "ChannelEffectiveTimeSearchSelect",
  props: {
    // 渠道生效时间对象
    ChannelEffectiveTime: {
      type: Object,
    },
    FeeFieldOptions:{
      type:Array
    }
  },
  data() {
    return {
      //下拉搜索框 相关
      options: [],
      value: [],
      list: [],
      loading: false,

      // 时间范围
      datetimerange: ["", ""],
    };
  },
  async created() {
    //console.log(this.ChannelEffectiveTime)
    // 初始化 数据

    for (let i = 0; i < this.ChannelEffectiveTime.fee_field.length; i++) {
      this.value.push(this.ChannelEffectiveTime.fee_field[i].id);
    }
    if (this.ChannelEffectiveTime.time_start) {
      this.datetimerange[0] = this.ChannelEffectiveTime.time_start;
    }
    if (this.ChannelEffectiveTime.time_end) {
      this.datetimerange[1] = this.ChannelEffectiveTime.time_end;
    }

    this.options = this.FeeFieldOptions
  },
  async mounted() {
    
  },
  methods: {
    async selChange(val) {
      this.$emit("selChange", val);
      console.log(this.value);

      this.loading = true;

      // 更新 fee_field 局部数据
      let subData = {};
      subData.fee_field = this.value;

      let { data } = await this.$axios.patch(
        `ChannelEffectiveTime/${this.ChannelEffectiveTime.id}/`,
        subData
      );

      this.loading = false;
      //console.log(data)
    },
    async dateChange() {
      // 更新 开始时间 结束时间 局部数据
      let subData = {};
      subData.time_start = this.datetimerange[0];
      subData.time_end = this.datetimerange[1];
      subData.id = this.ChannelEffectiveTime.id;

      // let { data } = await this.$axios.patch(
      //   `ChannelEffectiveTime/${this.ChannelEffectiveTime.id}/`,
      //   subData
      // );

      let { message } = await this.$axios.post(
        `ChannelEffectiveTime/update_effective_time/`,
        subData
      );

      this.$message.success(message);
      //console.log(data)
    },
    async remoteMethod(query) {
      if (query !== "") {
        this.loading = true;

        const { data } = await this.$axios("FeeField/", {
          params: {
            search: query,
            page: this.page,
          },
        });
        this.options = data;

        this.loading = false;
      } else {
        const { data } = await this.$axios("FeeField/");
        this.options = data;
      }
    },
  },
};
</script>

<style>
</style>


