<template>
  <section>
    <!--工具条-->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px">
      <el-form :inline="true" :model="filters">
        <el-form-item>
          <el-input
            v-model="filters.name"
            placeholder="用户名称"
            @keydown.enter.native="getOrders"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-on:click="getOrders">查询</el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="addDialog = true">新增</el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!--列表-->
    <el-table :data="orders" highlight-current-row style="width: 100%">
      <el-table-column type="selection" width="55"> </el-table-column>

      <el-table-column prop="chanel" label="渠道" sortable> </el-table-column>

      <el-table-column label="操作" width="150" fixed="right">
        <template scope="scope">
          <!-- <el-button size="small" @click="handleEdit(scope.$index, scope.row)">查看详情</el-button>-->
          <el-button @click="handleEdit(scope.$index, scope.row)"
            >编辑</el-button
          >

          <el-button
            type="danger"
            @click="delOrder(scope.$index, scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <!--工具条-->
    <el-col :span="24" class="toolbar">
      <el-pagination
        layout="prev, pager, next"
        @current-change="handleCurrentChange"
        :page-size="pageSize"
        :total="total"
        style="float: right"
      >
      </el-pagination>
    </el-col>

    <!--新增dialog  -->
    <el-dialog title="新增渠道" :visible.sync="addDialog">
      <el-form
        class="demo-form-inline"
        :model="addUserinfo"
        label-width="100px"
      >
        <el-form-item label="渠道">
          <el-input v-model="addUserinfo.chanel" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="addDialog = false">取 消</el-button>
        <el-button type="primary" @click="addUser()">提交</el-button>
      </div>
    </el-dialog>

    <!-- 新增dialog 结束 -->

    <!--编辑dialog  -->
    <el-dialog title="编辑渠道" :visible.sync="editDialog">
      <el-button type="primary" @click="addChannel()"> 增加渠道费率 </el-button>

      <el-form
        class="demo-form-inline"
        :model="editUserinfo"
        label-width="180px"
        :inline="true"
      >
        <el-form-item label="渠道">
          <el-input
            v-model="editUserinfo.chanel"
            auto-complete="off"
          ></el-input>
        </el-form-item>
      </el-form>

      <div
        style="border-bottom: 1px solid gray; margin-top: 25px; display: flex"
        v-for="(item, index) in feeArray"
        :key="index"
      >
        <div style="width: 90%; dispaly: inline-block">
          <search-select
            :ChannelEffectiveTime="item"
            :FeeFieldOptions="FeeFieldOptions"
          ></search-select>
        </div>

        <el-button
          style="height: 35px"
          @click="delChannelEffectiveTime(item, index)"
          >删除</el-button
        >
      </div>

      <div slot="footer" class="dialog-footer">
        <el-button @click="editDialog = false">取 消</el-button>
        <el-button type="primary" @click="editUser()">提交</el-button>
      </div>
    </el-dialog>

    <!-- 编辑dialog 结束 -->
  </section>
</template>


<script>
import SearchSelect from "./components/ChannelEffectiveTimeSearchSelect.vue";
export default {
  components: {
    SearchSelect,
  },
  data() {
    return {
      filters: {},
      orders: [],
      pageNum: 1,
      pageSize: 10,
      total: 0,
      addUserinfo: {
        quantity: 0,
        unit_price: 0,
      },
      addDialog: false,
      editDialog: false,
      editUserinfo: {},

      // 费率对象 数组  FeeField 下拉多选框数组 用于传给子组件
      feeArray: [],
      FeeFieldOptions: [],
    };
  },
  async created() {
    this.getOrders();
//获得 下拉多选框  选项 在这里只需获取一次
      const res = await this.$axios("FeeField/", {
        params: {
          page: 1,
          page_size: 1000,
        },
      });

      this.FeeFieldOptions = res.data;
   
  },
  methods: {
    handleCurrentChange(val) {
      this.page = val;
      this.getOrders();
    },
    async getOrders() {
      const data = await this.$axios("ChannelRate/", {
        params: {
          search: this.filters.name,
          page: this.page,
        },
      });

      console.log(data);

      this.orders = data.data;
      this.total = data.count;
    },
    handleEdit(index, row) {
      this.editDialog = true;
      this.editUserinfo = Object.assign({}, row);

      // 渠道逻辑 相关代码
      this.getChannelEffectiveTime();
    },

    async delChannelEffectiveTime(item, index) {
      let res = await this.$axios.delete(`ChannelEffectiveTime/${item.id}`);
      this.$message.success(res.message);
      this.feeArray.splice(index, 1);
    },

    // 获取渠道生效时间
    async getChannelEffectiveTime() {
      // 清空 渠道生效时间 数组
      this.feeArray.splice(0, this.feeArray.length);
      // 重新获得 渠道生效时间 表
      let { data } = await this.$axios(
        `ChannelEffectiveTime?chanel_id=${this.editUserinfo.id}`
      );

      //console.log(data);

      this.feeArray = data;

       
    },
    // 新增渠道
    async addChannel() {
      let { data } = await this.$axios.post("ChannelEffectiveTime/", {
        chanel_id: this.editUserinfo.id,
      });

      this.feeArray.push(data);
    },
    async delOrder(index, row) {
      let res = await this.$axios.delete(`ChannelRate/${row.id}`);
      console.log(res);
      this.getOrders();
    },
    async addUser() {
      let res = await this.$axios.post("ChannelRate/", this.addUserinfo);
      //console.log(res)
      this.addDialog = false;
      this.getOrders();
    },
    async editUser() {
      let res = await this.$axios.put(
        `ChannelRate/${this.editUserinfo.id}/`,
        this.editUserinfo
      );
      console.log(res);
      this.editDialog = false;
      this.getOrders();
    },
  },
};
</script>

<style>
</style>


